$activeColor: #4835ea;
$radius: 20px;
$width: 120px;

.root {
  background: rgba(white, 0.1);
  border-radius: $radius;
  padding: 2px;
  height: 40px;
  width: $width * 2;

  display: flex;
  margin: 0;
}
.border {
  border: 2px solid #{$activeColor};
}
.mobileRoot {
  width: $width;
}
.button {
  border-radius: $radius;
  min-width: 50%;
  width: 100%;
  height: 100%;

  color: white;
  font-weight: bold;
  font-size: 1rem;
  font-family: "Gilroy";
  text-transform: uppercase;

  background: transparent !important;
  box-shadow: none;
}
.active {
  background: $activeColor !important;
}
