.root {
  padding: 15px;
  background: transparent;
  overflow-y: hidden;
}
.gridContainer {
  // display: grid;
  gap: var(--gridGap);
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  overflow-y: auto;
}
.scrollBar {
  -webkit-overflow-scrolling: touch;

  &::-webkit-scrollbar-thumb {
    background: rgba(159, 141, 208, 0.5);
    border-radius: 30px;
  }
  &::-webkit-scrollbar-track {
    background: #2c2c4a;
    border-radius: 30px;
  }
  &::-webkit-scrollbar {
    background: #2c2c4a;
    width: 4px;
    border-radius: 30px;
  }

  & {
    scrollbar-face-color: rgba(159, 141, 208, 0.5);
    scrollbar-track-color: #2c2c4a;
  }
}
.gridItem {
  cursor: pointer;
  img {
    width: 100%;
    height: auto;
  }
}
