@import "sass/index";

.footer {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  overflow: hidden;
  padding: spacing(5.5);
  font-family: "Gilroy";
  color: use(--template-footer-colorTransparent);
  font-size: spacing(1.5);
  line-height: spacing(2.5);

  position: relative;
  z-index: 1;
  font-weight: 500;

  @media (max-width: $mobileScreen) {
    padding: spacing(4.25);
  }
}

.container {
  display: flex;
  & > div {
    padding: 7px;
  }
}

.christmas {
  $christmasTextColor: set(#9567e9);
  $christmasBGColor: #ebf1f4;

  --secondary-main: #{$christmasTextColor};
  --template-footer-colorTransparent: #{$christmasTextColor};
  --template-footer-select-background: #{$christmasTextColor};
  --template-footer-colorSemiTransparent: #{$christmasTextColor};
  --template-footer-icons-twitter-background: #{$christmasTextColor};
  --template-footer-logos-pasino-background: #{$christmasTextColor};

  --template-footer-select-color: #{set($christmasBGColor)};
  --template-footer-icons-twitter-innerBackground: #{set($christmasBGColor)};
  background: $christmasBGColor;

  & * {
    opacity: 1 !important;
  }
}
