@keyframes move {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

.root {
  width: fit-content;
  margin: 0 auto;
}

.dotsWrapper {
  display: flex;
  position: relative;
  justify-content: space-between;
}

.dot {
  background: rgba(#d8e6fa, 0.3);
  width: 6px;
  height: 6px;
  border-radius: 50%;
  &.secondary {
    width: 8px;
    height: 8px;
    background: rgba(10, 13, 23, 0.3);
    &.selected,
    &:hover {
      background: #34e7ff;
    }
  }
}

.moveDot {
  background: #d8e6fa;
  box-shadow: 0px 0px 8px 0px white;
  transition: transform 0.4s;
  &.secondary {
    background: #34e7ff;
  }
}

.moveDotWrapper {
  position: absolute;
  top: 0;
  transition: transform 0.4s;
}
