@import "sass/index";

.wrapper {
  display: flex;
  flex: 1;
  border-radius: 50%;
  overflow: hidden;
  width: max-content;
}
.image {
  max-width: 100%;
  max-height: 100%;
  width: 100%;
  height: 100%;
  opacity: 0;
  transition: opacity $defaultTransition;
}
.opacity {
  opacity: 1;
}
