@import "sass/index";
@import "components/Input/_input.module.scss";

.root {
  position: relative;
  width: 100%;
  margin-top: 1rem;
}

.label {
  position: absolute;
  font-size: 1rem;

  transform: translate(0, 1.5px) scale(0.75);
  transform-origin: top left;
}

.formInputLabel {
  color: use(--template-input-label-color);
}
