@import "sass/index";

.root {
  border: 0;
  margin: 0;
  display: inline-flex;
  padding: 0;
  position: relative;
  min-width: 0;
  flex-direction: column;
  vertical-align: top;
  width: 100%;
}

.inputWrapper {
  color: white;
  cursor: text;
  display: inline-flex;
  position: relative;
  font-size: 1rem;
  box-sizing: border-box;
  align-items: center;
  font-family: "Gilroy";
  font-weight: 400;
  background: rgba(255, 255, 255, 0.09);
  height: 60px;
  overflow: hidden;

  border-radius: 4px;
  border: 2px solid rgba(255, 255, 255, 0.1);

  transition: border-color $defaultTransition;

  &:focus-within,
  &:active,
  &:focus {
    border-color: rgba(255, 255, 255, 0.2);
  }
}

.input {
  padding: 27px 12px 10px;
  position: absolute;
  top: 0;
  left: 0;
}

.label {
  position: absolute;
  left: 10px;
  top: calc(50% - (1rem / 2));

  pointer-events: none;
  color: white;

  font-family: "Roboto", "Helvetica", "Arial", sans-serif;
  font-weight: 400;
  font-size: 1rem;
  line-height: 1rem;
  letter-spacing: 0.00938em;

  padding: 0;

  display: block;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  max-width: calc(100% - 24px);
  transform: translate(0, 0) scale(1);
  transform-origin: left top;
  transition: color 200ms cubic-bezier(0, 0, 0.2, 1) 0ms,
    transform 200ms cubic-bezier(0, 0, 0.2, 1) 0ms,
    max-width 200ms cubic-bezier(0, 0, 0.2, 1) 0ms;
  z-index: 1;
}

.focusedLabel {
  transform: translate(-3px, -12px) scale(0.75);
  opacity: 0.8;
}
