@import "sass/index";

.messageWrap {
  display: flex;
  padding-bottom: 10px;
  width: 100%;
  max-width: calc(100% - 5px);

  font-family: "Gilroy", sans-serif;
  min-height: 60px;
  height: fit-content;

  color: use(--template-chat-massage-textBlock-color);
  pointer-events: all;
}

.content {
  width: 100%;

  margin-left: 12px;
}

.desktop {
  // max-width: 266px;
}

.mobile {
}

.messageBlock {
  background: use(--template-chat-massage-textBlock-background);
  border-radius: 8px;
  padding: 8px 10px;
  box-shadow: -3px 2px 4px -1px #0000005c;
  line-height: 1.25;

  & :global .baseButton {
    transition: none;
  }

  & > * {
    pointer-events: none;
  }

  & a,
  & button {
    pointer-events: all;
  }
}

.imageBlock {
  background: transparent;
  box-shadow: none;

  & img {
    box-shadow: -3px 2px 4px -1px #0000005c;
  }
}

.contactMessage {
  background: use(--template-chat-massage-contactBlock-background);
  color: use(--template-chat-massage-contactBlock-color);

  & :global .baseButton {
    color: use(--template-chat-massage-contactBlock-background);
    background: use(--template-chat-massage-contactBlock-color);
  }
}
