$arrowURL: "./arrow.svg";

.root {
  position: fixed;
  z-index: 9999;
  width: 100%;
  height: 100%;
  background: rgba(238, 225, 214, 1);
  border-radius: 10px;
  margin: auto;
}

.triangle {
  width: 0;
  height: 0;
  position: fixed;
  z-index: 9999;
  border-left: calc(var(--triangleWidth) / 2 * 1px) solid transparent;
  border-right: calc(var(--triangleWidth) / 2 * 1px) solid transparent;
  border-bottom: calc(var(--triangleHeight) * 1px) solid rgba(238, 225, 214, 1);
  transform: translate(-50%, -100%);
}
