@import "sass/index";

.root {
  margin-top: 10px;
  position: relative;
  padding-bottom: 6px;
}

.inputWrapper {
  display: flex;
  justify-content: space-between;
  padding-right: 10px;
}

.rainIcon {
  height: 16px;
  width: 16px;
  background-color: rgba(0, 0, 0, 0.35);
  margin-right: 6px;
  box-sizing: content-box;

  & > * {
    position: absolute;
  }

  & * {
    fill: #ffffff;
  }
  @media (max-width: 600px) {
    box-sizing: border-box;
    height: 40px;
    width: 40px;
  }
}
