@import "sass/index";

.root {
  background: none;
  border: none;
  position: relative;
  overflow: hidden;
  outline: none;
  cursor: pointer;
  color: use(--secondary-main);
}
.item {
  position: absolute;
  top: 0;
  left: 0;
  transition: transform 0.4s;
}

.disable {
  transform: translateY(-200%);
}

.icon {
  fill: currentColor;
}
