.FallbackPage_appBar > *,
.FallbackPage_wrapper > * {
  overflow: hidden;
}
html {
  height: 100%;
  font-display: optional;
}
body {
  margin: 0;
  height: 100%;
  background-color: rgb(48, 27, 82);
}
#root {
  display: flex;
  height: 100%;
  flex-direction: column;
}
.FallbackPage_appBar {
  display: flex;
  flex-direction: row;
  height: 64px;
  padding: 0 24px;
  position: relative;
  align-items: center;
}

.FallbackPage_menuButton {
  margin: 12px;
  margin-left: 0;
  min-width: 32px;
  min-height: 32px;
  width: 32px;
  height: 32px;
}

.FallbackPage_logos {
  display: flex;
  align-items: center;
}

.FallbackPage_grow {
  display: flex;
  flex: 1 1 auto;
}

.FallbackPage_icon {
  margin: 12px;
  min-width: 32px;
  min-height: 32px;
}

.FallbackPage_logo {
  height: 46px;
  width: 135px;
  margin-left: 28px;
}

.FallbackPage_desktop {
  display: block;
}

@media only screen and (max-width: 700px) {
  .FallbackPage_desktop {
    display: none;
  }
}

.FallbackPage_select {
  border-radius: 8px;
  margin: 0 8px;
}
.FallbackPage_block {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.FallbackPage_wrapper {
  padding: 32px;
  display: flex;
  flex: 1;
  justify-content: center;
  box-sizing: border-box;
}

.FallbackPage_innerWrapper {
  display: flex;
  flex-direction: row;
  max-width: 1920px;
  width: 100%;
}
.FallbackPage_content {
  max-width: 100%;
  display: flex;
  flex: 1;
  flex-direction: column;
  position: relative;
}

.FallbackPage_gameBlock {
  display: flex;
  height: 360px;
  min-height: 360px;
  width: 100%;
  border-radius: 24px;
  overflow: hidden;
}

.FallbackPage_rowTitle {
  display: flex;
  flex-direction: row;
  margin: 28px 22px;
}

.FallbackPage_row {
  max-width: 100%;
  /* display: flex; */
  flex-direction: row;
  padding-top: 10px;
  margin: 0 22px;
}

.FallbackPage_banner {
  height: 220px;
  width: 190px;
  max-width: 190px;
  border-radius: 30px;
  overflow: hidden;
  display: inline-block;
}
.FallbackPage_banner:not(:first-child) {
  margin-left: 20px;
}

.FallbackPage_chat {
  display: flex;
  flex-direction: column;
  min-width: 280px;
  /* flex: 1 280px; */
  height: 100%;
  margin-left: 32px;
  overflow: hidden;
}

@media only screen and (max-width: 1039px) {
  .FallbackPage_banner:nth-child(3) {
    display: none;
  }
}

@media only screen and (max-width: 819px) {
  .FallbackPage_chat {
    display: none;
  }
}

@media only screen and (max-width: 507px) {
  .FallbackPage_banner:nth-child(2) {
    display: none;
  }
  .FallbackPage_banner {
    display: flex;
  }
  .FallbackPage_row {
    display: flex;
    justify-content: center;
  }
}

.FallbackPage_chatMessages {
  overflow: hidden;
  display: flex;
  flex: 1;
  border-radius: 16px;
  margin-bottom: 24px;
}

.FallbackPage_chatInput {
  overflow: hidden;
  display: flex;
  height: 40px;
  width: 100%;
  border-radius: 20px;
}
