@import "sass/index";

$headerSelectorHeight: 56px;
$inputHeight: 56px;

$hideAnimationTime: 500ms;

.root {
  position: fixed;
  top: $desktopHeaderSize;
  right: 0;
  z-index: 5;
  width: 300px;

  --messages-height: calc(
    100vh - #{$desktopHeaderSize} - #{$headerSelectorHeight} - #{$inputHeight}
  );
  pointer-events: none;
  transition: transform $hideAnimationTime;

  & > * {
    pointer-events: all;
  }
}

.content {
  position: relative;
  z-index: 5;

  background: var(--template-page-background);
  background-color: Hsla(var(--template-page-backgroundColor));
  padding-left: 5px;
}

.hide {
  transform: translateX(100%);
}

.disabled {
  display: none;
}

:export {
  #{"hideAnimationTime"}: $hideAnimationTime;
}
