@import "sass/index";

.statisticsBlock {
  height: 730px;
  border-radius: 0 0 10px 10px;
  overflow: hidden;
  background: use(--template-statisticsBlock-background);

  @media (max-width: $mobileScreen) {
    background: use(--template-statisticsBlock-mobile-background);
  }
}

.christmas {
  $newBG: var(--decorations-christmas);
  --template-statisticsBlock-background: #{$newBG};
  --template-statisticsBlock-mobile-background: #{$newBG};
}
