.root {
  background: #000000;
  border-radius: 10px;
  width: 300px;
  z-index: 5;
  margin-top: -15px;
  display: flex;
  flex-direction: column;

  &:after {
    content: "";
    position: absolute;
    top: 100%;
    left: 75%;
    transform: translateX(-50%);

    width: 0;
    height: 0;
    border-left: 8px solid transparent;
    border-right: 8px solid transparent;
    border-top: 8px solid #000000;
  }

  @media (max-height: 690px) {
    height: calc(
      100vh - 60px - 90px - 60px
    ); // fool screen - header - footer - input
  }
  @media (max-width: 600px) {
    width: calc(100vw - 15px);
    margin-left: 15px;
  }
}
.content {
  flex-grow: 1;
  overflow: hidden;
}
