@import "sass/index";

.text {
  color: use(--template-gameCell-switch-selectedText);
  text-transform: uppercase;
  font-weight: bold;
  margin: 0;
  cursor: pointer;

  @media (max-width: 450px) {
    font-size: 0.685rem;
  }
}

.disableText {
  opacity: 0.5;
  color: use(--template-gameCell-switch-selectedText);
}

.switcher {
  display: flex;
  justify-content: center;
  align-items: center;
}

.switchTrack {
  border-radius: 12px;
  height: 22px;
}

.switchToggle {
  width: 16px;
  height: 16px;
}

.switch {
  margin-left: 12px;
  margin-right: 12px;
  width: 44px;
}
