@import "sass/index";

@mixin setTypography(
  $fontSize,
  $fontWeight,
  $lineHeight,
  $letterSpacing,
  $textTransform: ""
) {
  color: use(--secondary-main);
  font-size: $fontSize;
  font-family: "Gilroy";
  font-weight: $fontWeight;
  line-height: $lineHeight;
  letter-spacing: $letterSpacing;
  text-transform: $textTransform;
}

.root {
  margin: 0;
}

.body {
  &1 {
    @include setTypography(1rem, 400, 1.5, 0.00938em);
  }

  &2 {
    @include setTypography(0.875rem, 400, 1.43, 0.01071em);
  }
}

.button {
  @include setTypography(0.875rem, 500, 1.75, 0.02857em, uppercase);
}

.caption {
  @include setTypography(0.75rem, 400, 1.66, 0.03333em);
}

.overline {
  @include setTypography(0.75rem, 400, 2.66, 0.08333em, uppercase);
}

.srOnly {
  width: 1px;
  height: 1px;
  overflow: hidden;
  position: absolute;
}

.subtitle {
  &1 {
    @include setTypography(1rem, 400, 1.75, 0.00938em);
  }

  &2 {
    @include setTypography(0.875rem, 500, 1.57, 0.00714em);
  }
}

.h {
  &1 {
    @include setTypography(6rem, 300, 1.167, -0.01562em);
  }

  &2 {
    @include setTypography(3.75rem, 300, 1.2, -0.00833em);
  }
  &3 {
    @include setTypography(3rem, 400, 1.167, 0em);
  }
  &4 {
    @include setTypography(2.125rem, 400, 1.235, 0.00735em);
    font-size: 2.125rem;
  }
  &5 {
    @include setTypography(1.5rem, 400, 1.334, 0em);
  }
  &6 {
    @include setTypography(1.25rem, 500, 1.6, 0.0075em);
  }
}
