$inputIndent: 10px;

.root {
  display: inline-flex;
  align-items: center;
  background-color: white;
  font-size: 16px;
  padding-left: $inputIndent;
  padding-right: $inputIndent;
  height: 40px;
  width: 100%;
}

.input {
  display: block;
  width: 100%;
  outline: none;
  background-color: transparent;
  border: none;
  padding: 0;
  font: inherit;
  color: currentColor;
  &:disabled {
    filter: brightness(0.6);
  }
}

.error {
  border: 2px solid red;
}
