.root {
  width: 100%;
  max-width: 320px;

  position: fixed;
  z-index: 1300;
  left: 0;
  top: 0;

  border-radius: 10px;
  overflow: hidden;
  box-shadow: 0px 10px 15px 5px rgba(0, 0, 0, 0.7);

  color: white;

  background: linear-gradient(131.55deg, #24375f -1.38%, #000000 60.66%),
    #0a0d18;
}
.disable {
  display: none;
}

.fallback {
  min-height: 150px;
  display: flex;
  justify-content: center;
  align-items: center;
}
