@import "sass/index";

$roundSize: 20px !default;
$radioColor: use(--template-input-topLabel-color);

.radio {
  position: relative;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  width: $roundSize;
  height: $roundSize;
  background-color: transparent;
  border-radius: 50%;
  border: 2px solid $radioColor;
  text-align: center;
  margin-right: 10px;
  &::before {
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    border-radius: 50%;
    transform: scale(0);
    background-color: rgba(white, 0.1);
    transition: transform 0.3s;
  }
  &Pressed {
    &::before {
      transform: scale(2.5);
    }
  }
}

.input {
  position: absolute;
  width: 100%;
  height: 100%;
  margin: 0;
  opacity: 0;
  cursor: pointer;
}

.round {
  width: $roundSize - 10px;
  height: $roundSize - 10px;
  background-color: currentColor;
  border-radius: 50%;
  pointer-events: none;
  transition: transform 0.2s;
  transform: scale(0);
  &Checked {
    transform: scale(1);
  }
}

.root {
  width: 100%;
}

.label {
  color: $radioColor;
  display: inline-flex;
  font-family: "Gilroy";
  font-size: 16px;
  cursor: pointer;
  & > div {
    flex-shrink: 0;
  }
  &Text {
    display: inline-block;
    color: currentColor;
  }
}
