$emojiIndent: 5px;

.root {
  height: 100%;
  overflow-x: hidden;
}

.gifContainer {
  overflow-y: scroll;
  max-height: 320px;
  height: calc(
    100vh - 60px - 90px - 60px - 60px - 10px - 15px - 75px
  ); //fScreen - (header+footer+input+search+tenor+tabs)
  margin: 0 !important;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  width: 100%;
  padding: 10px;
  gap: $emojiIndent;
  & > div {
    height: 100px;
    & > img {
      object-fit: cover;
      height: 100%;
      width: 100%;
    }
  }
}

.input {
  position: relative;
  width: calc(100% - 32px);
  margin: 13px 14px 16px 14px;
  background: rgba(118, 118, 128, 0.24);
  border-radius: 8px;
  & > input {
    padding-left: 5px;
    color: #ffffff;
  }
}

.image {
  cursor: pointer;
}

.tenor {
  margin-left: 14px;
  width: 50%;
  height: auto;
  margin-top: 5px;
}

.formInputAdornment {
  padding: 0;
  width: 16px;
}

.formInputAdornmentIcon {
  height: 16px;
  width: 16px;

  & > * {
    position: absolute;
  }

  & * {
    fill: #ffffff;
  }
}
