.root {
  display: block;
  &Close {
    // width: auto;
    // pointer-events: none;
    width: 0;
  }
}

.drawer {
  // display: inline-block;
  position: absolute;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100%;
  &Close {
    pointer-events: none;
    transform: translateX(-100%) !important;
  }
}

.tail {
  touch-action: none;
  display: none;
  position: absolute;
  height: 100%;
  left: 0;
  top: 0;
  @media (max-width: 600px) {
    display: block;
  }
}
