@import "sass/index";

.button {
  position: absolute;
  z-index: 10;
  bottom: 37px;
  right: 15px;

  border-radius: 50%;
  padding: 0;

  transition: transform 0.4s, opacity 0.4s;
  transform: translateY(0px);
  box-shadow: 0px 2px 5px 3px rgba(0, 0, 0, 0.6);
}

.disable {
  transform: translateY(35px);
  opacity: 0;
  pointer-events: none;
}

.arrow {
  width: 35px;
  height: 35px;
}
