.root {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;

  position: absolute;
  top: 0;
  left: 0;
  z-index: 0;
}
.arrow {
  margin-top: 15px;
  height: 35px;
  width: 25px;

  display: flex;
  align-items: flex-end;
  justify-content: center;
  border-radius: 5px;
  clip-path: polygon(0 0, 100% 0%, 100% 75%, 50% 100%, 0 76%);
}
.text {
  margin: 0;
  margin-bottom: 3px;

  font-family: "Gilroy", sans-serif;
  font-size: 0.625rem;
  line-height: 1rem;

  color: #150a30;
  font-weight: bold;
}
