@import "sass/index";

.logoWrapper {
  display: flex;
  justify-content: flex-start;
}
.logo {
  & path {
    fill: use(--template-footer-logos-pasino-background);
    opacity: use(--template-footer-logos-pasino-opacity);
  }
}
.copyright {
  padding-top: spacing(2.5);
}
.rights {
}
.leftColumn {
  display: flex;
  flex-direction: column;
  flex-grow: 0;
  max-width: 25%;
  flex-basis: 25%;
}
.description {
  display: flex;
  flex-direction: column;
  flex: 1;
}
