@import "sass/index";

.text {
  @media (min-width: 600px) {
    font-size: 16px;
  }

  font-family: "HelveticaNeue", san-serif;
  font-size: 12px;

  color: inherit;
  letter-spacing: 0;
  overflow-wrap: anywhere;
  word-break: break-word;
  display: inline;

  & img {
    width: auto;
    height: 16px;
    margin-bottom: -2px;
  }
}

.link {
  font-weight: bold;
}
