.value {
  color: #fff;
  font-size: 18px;
  font-family: "Gilroy";
  font-weight: bold;
  margin-right: auto;
  margin-left: 15px;
  @media (max-width: 500px) {
    font-size: 16px;
  }
}

.coinSelect {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  padding-right: 5%;
}

.coinImg {
  position: static;
  width: 38px;
  height: 38px;
}

.label {
  color: rgba(255, 255, 255, 0.5);
  font-size: 23px;
  text-align: right;
  font-family: "Gilroy";
  font-weight: normal;
  @media (max-width: 500px) {
    font-size: 18px;
  }
}

.root {
  background-color: rgba(255, 255, 255, 0.1);
}
