$expandIconSize: 20px;

.select {
  background-color: rgba(gray, 0.5);
  border-radius: 8px;
  cursor: pointer;
  display: flex;
  max-height: 60px;
  justify-content: space-between;
  align-items: center;
  padding-left: 15px;
  padding-right: 15px;
}

.options {
  position: absolute;
  border-radius: 4px;
  overflow: hidden;
  animation-name: emergence;
  animation-duration: 0.15s;
  max-height: 80%;
  min-width: max-content;
  overflow-y: auto;
  transform-origin: bottom;
  &Upwards {
    transform-origin: top;
  }
}

@keyframes emergence {
  from {
    opacity: 0;
    transform: scale(0.8, 0.3);
  }
  to {
    opacity: 1;
    transform: scale(1, 1);
  }
}

.arrow {
  background-color: transparent;
  height: $expandIconSize;
  & > svg {
    width: $expandIconSize;
    height: $expandIconSize;
    transition: transform 0.2s;
  }
  &Open {
    & > svg {
      transform: rotate(-180deg);
    }
  }
}

.title {
  color: white;
  font-family: "Gilroy";
  font-weight: bold;
  font-size: 25px;
}

.disabled {
  filter: brightness(0.7);
  cursor: not-allowed;
}
