@import "sass/index";

.select {
  font-family: "Gilroy", sans-serif;
  font-weight: bold;
  color: use(--template-footer-select-color);
  height: 40px;
  border-radius: 4px;
  padding-right: 6px;
  padding-left: 11px;
  background-color: use(--template-footer-select-background);

  &Label {
    // margin-right: 25px;
    & > span {
      width: 80px;
      display: block;
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
      font-family: "Gilroy", sans-serif;
      font-weight: bold;
      color: use(--template-footer-select-color);
      font-size: 13px;
    }
  }

  &Icon {
    transform: scale(0.5);
    & polyline {
      stroke: use(--template-footer-select-color);
    }
  }

  // & > div {
  //   background: use(--template-footer-select-background) !important;
  // }
  // & svg {
  //   top: inherit;
  //   right: 11px;
  // }
  // & svg g {
  //   stroke: use(--template-footer-select-arrowIcon-color);
  // }
}
.input {
  background: use(--template-footer-select-background);

  font-size: 13px;
  color: use(--template-footer-select-color);
}
.fontFamily {
  font-family: "Gilroy";
  font-weight: bold;
}
.menuBackground {
}
.menuItem {
  font-family: "Gilroy";
  font-weight: bold;

  color: use(--template-footer-select-menuItem-color);
}
