@import "sass/index";

$inputHeight: 40px;
$inputWidthDesktop: 190px;
$inputWidthMobile: calc(100vw - 172px);

.root {
  position: relative;
  width: calc(100% - 50px);
  min-width: 230px;
  height: $inputHeight;
}
.input {
  font-family: "HelveticaNeue", "Helvetica", san-serif;
  font-size: spacing(2);

  position: absolute;
  z-index: 1;
  top: 0;
  left: 0;
  width: 100%;
  height: $inputHeight;
  outline: none;

  cursor: text;
}
.div {
  border-radius: 20px;
  box-sizing: border-box;

  background-color: rgba(0, 0, 0, 0.35);
  width: 100%;
  transition: border-color $defaultTransition, box-shadow $defaultTransition;

  color: rgba(255, 255, 255, 0.9);

  padding: spacing(2);
  padding-right: 0;

  display: flex;
  align-items: center;
}
.divFocused {
  box-shadow: use(--secondary-main, 0.25) 0 0 0 0.2rem;
  border-color: use(--secondary-main);
}
.renderText {
  $left: 15px;
  $adornments: 64px;
  $padding: 10px;

  position: absolute;
  left: $left;
  width: calc(100% - #{$left} - #{$adornments} - #{$padding});

  max-height: $inputHeight;
  overflow-x: auto;
  overflow-y: hidden;
  outline: none;

  white-space: nowrap;

  margin-top: 1px;

  -ms-overflow-style: none;
  scrollbar-width: none;

  & img {
    max-height: 20px;
    margin-bottom: -2.5px;
    max-width: 19px;
    object-fit: contain;
  }

  &::-webkit-scrollbar {
    display: none;
  }

  @media (max-width: 600px) {
    width: $inputWidthMobile;
  }
}

.placeholder {
  color: #ffffff;
  opacity: 0.75;

  position: absolute;
  z-index: 0;
  height: $inputHeight;
  padding-left: 15px;
  display: flex;
  align-items: center;
}

.formAdornment {
  position: absolute;
  z-index: 2;
  right: 10px;
  top: 0;
  height: $inputHeight;

  display: flex;
  align-items: center;
}
.formInputAdornmentIcon {
  height: 32px;
  width: 32px;
  box-sizing: border-box;

  & > * {
    position: absolute;
  }

  & * {
    fill: #ffffff;
  }
}
