@import "sass/index";

.head {
  display: table-header-group;
}

.tr {
  color: inherit;
  display: table-row;
  outline: 0;
  vertical-align: middle;
}

.th {
  padding: 20px;
  font-weight: 500;
  line-height: 15px;
  display: table-cell;
  font-size: 0.875rem;
  text-align: center;
  font-family: "Gilroy";
  border-bottom: 1px solid use(--secondary-main, 0.2);
  letter-spacing: 0.01071em;
  vertical-align: inherit;

  font-weight: bold;
  color: use(--template-statisticsBlock-tab-tableHead);

  @media (max-width: $mobileScreen) {
    padding: 10px;
  }
}
