@import "sass/index";

$animationTime: 0.7s;

@keyframes weave {
  0% {
    transform: scale(0);
    opacity: 0.3;
  }

  100% {
    transform: scale(100);
  }
}

.baseButton {
  transition: color $defaultTransition, opacity $defaultTransition,
    background-color $defaultTransition, box-shadow $defaultTransition,
    background $defaultTransition;

  font-family: "Gilroy", sans-serif;
  color: use(--template-button-color-main);
  border-radius: 4px;
  background-color: use(--template-button-backgroundColor-main);
  overflow: hidden;

  display: inline-flex;
  align-items: center;
  justify-content: center;
  position: relative;
  box-sizing: border-box;
  outline: 0;
  border: 0;
  margin: 0;
  padding: 0;
  cursor: pointer;
  user-select: none;
  vertical-align: middle;
  text-decoration: none;
  font-family: "Gilroy", "Helvetica", "Arial", sans-serif;
  font-weight: bold;
  font-size: 0.875rem;
  line-height: 1.75;
  letter-spacing: 0.02857em;
  text-transform: uppercase;
  // min-width: 64px;
  padding: 6px 16px;
  box-shadow: 0px 3px 1px -2px RGB(0 0 0 / 20%),
    0px 2px 2px 0px RGB(0 0 0 / 14%), 0px 1px 5px 0px RGB(0 0 0 / 12%);

  &:hover {
    background-color: use(--template-button-backgroundColor-hover-main);
  }
  &:focus {
    background-color: use(--template-button-backgroundColor-focus-main);
  }
  &:active {
    background-color: use(--template-button-backgroundColor-active-main);
  }
  &:disabled {
    background-color: use(--template-button-backgroundColor-disabled-main);
    opacity: 0.5;
    cursor: default;
  }

  &_transparent {
    color: use(--template-button-color-transparent);
    background-color: use(--template-button-backgroundColor-transparent);

    &:hover {
      background-color: use(
        --template-button-backgroundColor-hover-transparent
      );
    }
    &:focus {
      background-color: use(
        --template-button-backgroundColor-focus-transparent
      );
    }
    &:active {
      background-color: use(
        --template-button-backgroundColor-active-transparent
      );
    }
    &:disabled {
      background-color: use(
        --template-button-backgroundColor-disabled-transparent
      );
    }
  }

  &_icon {
    background-color: transparent;
    width: fit-content;
    height: fit-content;
    padding: 0;
    box-shadow: none;
    border-radius: 50%;

    &:hover {
      background-color: transparent;
    }
    &:focus {
      background-color: transparent;
    }
    &:active {
      background-color: transparent;
    }
    &:disabled {
      background-color: transparent;
    }
  }

  &_base {
    padding: 0;
  }

  &__label {
    width: 100%;
    display: inherit;
    align-items: inherit;
    justify-content: inherit;
  }

  &__waveAnimation {
    position: absolute;
    z-index: 10;
    background: white;
    opacity: 0;
    top: 0;
    left: 0;
    width: 5px !important;
    height: 5px !important;
    border-radius: 50%;

    &_play {
      animation: weave $animationTime ease-in-out backwards;
    }
  }
}
