@import "sass/index";

.wrapper {
  display: flex;
  justify-content: center;

  @media (max-width: $mobileScreen) {
    justify-content: flex-start;
    overflow: auto;
  }
}
.root {
  display: flex;
  justify-content: center;
  padding: 0 spacing(4) spacing(4);

  @media (max-width: 400px) {
    padding: 0 spacing(1) spacing(1);
  }
}
.innerWrapper {
  width: 100%;
  max-width: 100%;
}
