@import "sass/index";

.avatarWrapper {
  position: relative;
}
.avatarLink {
  width: fit-content;
  height: fit-content;
  border-radius: 50%;
}
.avatar {
  width: 32px;
  height: 32px;
  flex-basis: 32px;
  flex-shrink: 0;
  background: use(--secondary-main);

  position: relative;
  z-index: 1;

  & img {
    width: 32px;
    height: 32px;
  }
}
