@keyframes openAnimation {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

@keyframes closeAnimation {
  0% {
    opacity: 1;
  }

  100% {
    opacity: 0;
  }
}

.root {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1300;
  width: 100vw;
  height: 100vh;
}

.curtain {
  position: absolute;
  width: 100%;
  height: 100%;

  &__transparent {
    background: transparent;
  }
  &__dark {
    background: rgba(0, 0, 0, 0.5);
  }
}

.paper {
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  // position: relative;
}

.open {
  animation: openAnimation 0.15s ease-out forwards;
}

.close {
  animation: closeAnimation 0.15s ease-out forwards;
}
