.root {
  max-width: 400px;
  width: 100%;
  border-radius: 10px;
  background: linear-gradient(
      135deg,
      #3b1871 0%,
      rgba(11, 4, 21, 0.181709) 48.48%,
      rgba(3, 1, 4, 0.0001) 80.79%,
      #000000 100%
    ),
    #0a0d18;
  padding: 20px;

  @media (max-width: 600px) {
    margin: 0;
    max-width: 100%;
    padding: 10px;
  }
}
