.root {
  height: 400px;
  width: 100%;

  & > div {
    width: auto !important;
  }

  @media (max-height: 690px) {
    $marginTop: 10px;
    $marginBottom: 20px;
    height: calc(
      100vh - 60px - 90px - 60px - 60px - #{$marginTop} - #{$marginBottom - 5}
    );
    margin: $marginTop 0 $marginBottom;
  }
}
.grid {
  margin: 0 auto;
  overflow-x: hidden;
}
