@import "sass/index";

.nameBlock {
  display: flex;
  justify-content: space-between;

  margin-bottom: 5px;
}
.userName {
  max-width: calc(100% - 90px);
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;

  color: use(--template-chat-massage-author-color);
  font-weight: 700;
  font-size: 13.5px;
  letter-spacing: 0;
  line-height: 1;

  @media (min-width: 600px) {
    font-size: 15px;
  }
}
.streamer {
  color: #9156ff;
}
.moderator {
  color: use(--template-chat-massage-moderator-color);
}
.preEmoji {
  height: 15px;
  margin-right: 3px;
}
