@import "sass/index";

$darkenWhite: rgb(234, 234, 234);

.button {
  transition: color $defaultTransition, background-color $defaultTransition,
    box-shadow $defaultTransition;

  font-weight: bold;
  text-decoration: none;
  color: #090c16;

  background: white;

  &:active,
  &:focus,
  &:hover {
    background-color: $darkenWhite;
  }

  &:disabled {
    background-color: rgba(white, 0.5);
  }
}

.transparent {
  color: rgba(white, 0.5);
  background: transparent;

  &:active,
  &:focus,
  &:hover {
    background-color: rgba($darkenWhite, 0.2);
  }

  &:disabled {
    background-color: rgba(white, 0.1);
  }
}
