@import "sass/index";

$transitionTime: 400ms;

:export {
  #{"transitionTime"}: $transitionTime;
}

.root {
  position: fixed;
  bottom: 0;
  left: 0;
  z-index: 4;

  width: 100vw;
  height: calc(100vh);

  background: var(--template-page-background);
  background-color: Hsla(var(--template-page-backgroundColor));

  transition: transform #{$transitionTime};
  opacity: 0;
}

.ready {
  opacity: 1;
}

.hidden {
  transform: translateX(100vw);
  pointer-events: none;
}

.content {
  --messages-height: calc(
    100vh - #{$mobileNavigationFullSize} - #{$mobileHeaderSize} - 120px
  );
  padding: 0 4px 0 10px;
  position: absolute;
  left: 0;
  bottom: $mobileNavigationFullSize;
  width: 100%;
}

.disabled {
  display: none;
}
