.GamePageFallback_gameBlock {
  display: flex;
  height: 500px;
  width: 100%;
  border-radius: 24px;
  overflow: hidden;
}

.GamePageFallback_controlBlock {
  display: grid;
  grid-gap: 10px;
  gap: 10px;
  align-items: center;
  justify-items: center;
  grid-template-columns: repeat(3, 1fr);
  max-width: 700px;
  width: 100%;
  margin: 20px auto;
}

.GamePageFallback_controlSwitch {
  max-width: 80px;
  width: 100%;
  height: 25px;
  border-radius: 24px;
  overflow: hidden;
}

.GamePageFallback_controlButton {
  max-width: 200px;
  border-radius: 8px;
  width: 100%;
  height: 50px;
  overflow: hidden;
}

.GamePageFallback_inputBlock {
  max-width: 700px;
  width: 100%;
  margin: 20px auto;
  height: 40px;
  overflow: hidden;
  border-radius: 4px;
}
