@import "sass/index";

.mobileFooter {
  padding-bottom: 90px;
}

.innerWrapper {
}
.block {
}
.block1 {
  display: flex;
  flex-direction: row;
  padding-bottom: spacing(2);
}
.block2 {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;

  padding-top: spacing(3.75);
  padding-bottom: spacing(5.6);
}
.aboutWrapper {
  width: 100%;
}
.aboutInnerWrapper {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  width: 100%;

  & > :last-child {
    margin-left: 40px;
    max-width: 200px;
  }
}

.block3 {
  padding-top: spacing(5.6);
}
.divider {
  background-color: use(--template-footer-mobile-divider);
  flex: 1;
  height: 1px;
  width: 100%;
}

.copyrightBlock {
  margin-top: spacing(2.5);
}

.description {
  color: use(--template-footer-mobile-color);
  font-size: 12px;
  line-height: 22px;
  padding-bottom: spacing(2.5);
}
.copyright {
  color: use(--template-footer-mobile-color);
  font-size: 12;
  line-height: 20px;
}

.logoWrapper {
  padding-bottom: spacing(2);
  width: 100%;
  display: flex;
  justify-content: center;
}
.logo {
  & path {
    fill: use(--template-footer-logos-pasino-background);
    opacity: use(--template-footer-logos-pasino-opacity);
  }
  & {
    height: spacing(5);
    width: spacing(14.25);
  }
}

.aboutBlock {
  display: flex;
  flex-direction: column;

  & a {
    white-space: nowrap;
    text-decoration: none;
    color: use(--template-footer-mobile-color);
    font-family: "Gilroy", sans-serif;
    font-weight: bold;
    font-size: spacing(2);

    &:not(:first-child) {
      margin-top: spacing(3.5);
    }
  }
}
.select {
  width: 115px;
}
.social {
  margin-top: spacing(2.5);
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
}
.breadcrumbs {
  display: flex;
  flex-direction: column;
  margin-top: spacing(3);

  & a {
    text-decoration: none;
    color: use(--template-footer-mobile-color);
    font-family: "Gilroy", sans-serif;
    font-size: spacing(1.9);

    &:not(:first-child) {
      margin-top: spacing(3.5);
    }
  }
}

.icon {
  & g #UI {
    opacity: 1;
  }
}
.socialIcon {
  height: spacing(3.5);
  width: spacing(3.5);
  border-radius: 50%;
  overflow: hidden;

  // background: use(--template-footer-icons-twitter-background);
  background: transparent;

  display: flex;
  align-items: center;
  justify-content: center;

  margin: 5px;

  & img {
    height: 100%;
    width: 100%;

    // & path {
    // fill: use(--template-footer-icons-twitter-innerBackground);
    // }
  }
}

.url {
  margin-left: 5px;
  text-decoration: none;
  color: use(--template-footer-mobile-color);
  cursor: pointer;
}
