$arrowSize: 30;
$slideMargin: 5;

.arrow {
  background: white;

  min-height: #{$arrowSize}px;
  min-width: #{$arrowSize}px;
  height: #{$arrowSize}px;
  width: #{$arrowSize}px;

  border-radius: 50%;

  display: flex;
  align-items: center;
  justify-content: center;

  position: absolute;
  margin-top: #{- ($arrowSize / 2)}px;
  top: 50%;
  z-index: 3;

  cursor: pointer;
  transition: transform 0.4s;
  color: black;

  &:hover {
    transform: scale(1.2);
  }

  & svg {
    color: white;
  }
}
.leftArrow {
  left: 5px;
}
.rightArrow {
  right: #{(5 + $slideMargin)}px;
}

.btnLabel {
  width: 24px;
}
