@import "sass/index";

.dialogActions {
  padding-top: spacing(4.5);
  padding-bottom: spacing(3);
  padding-right: spacing(5.5);
  padding-left: spacing(4);
}
.title {
  font-family: "Gilroy";
  font-weight: bold;
  font-size: 14px;
  color: white;
  letter-spacing: 1px;
  text-transform: uppercase;
}
.closeIcon {
  margin-right: spacing(-2);

  & path {
    stroke: white;
  }
}
.grow {
  flex: 1 1 auto;
}
