@import "sass/index";

$radioIndent: 10px;

.label {
  display: block;
  font-weight: bold;
  font-size: 14px;
  color: use(--template-input-topLabel-color);
  margin-bottom: $radioIndent;
  &:disabled {
    cursor: not-allowed;
  }
}

.radioGroup {
  width: 100%;
  & > div {
    margin-bottom: $radioIndent;
  }
}
