$selectIndent: 10px;

@mixin item {
  cursor: pointer;
  background-color: white;
  color: black;
  font-family: "Varela Round";
  font-size: 1rem;
  width: 100%;
  padding-left: $selectIndent;
  padding-right: $selectIndent;
  padding-top: $selectIndent;
  padding-bottom: $selectIndent;
  transition: background-color 0.2s;
  &:hover {
    background-color: #e8e8f2;
  }
}
