@import "sass/index";

.box {
  margin: 20px;
  min-width: 300px;
  width: min-content;
}

.title {
  text-transform: uppercase;
  font-size: 0.875rem;
  margin: 0;

  color: use(--info-contrastText);
}
.formBox {
  border: none;
}
.inputWrapper {
}
label.label,
.label {
  color: use(--info-contrastText);
}
.radioRound {
}
.radio {
  border-color: use(--info-contrastText) !important;
  &Round {
    background-color: use(--info-contrastText) !important;
  }
}
.checkbox,
.checkbox > div {
  border-color: use(--info-contrastText);
  color: use(--info-contrastText);
  margin: 0;
}
.checkbox > div {
  background-color: use(--info-contrastText);
}
.check {
  color: use(--info-contrastText);
}

.input {
  height: 40px;
  max-width: 100px;
  width: 100%;

  background: transparent;
  border: none;
  color: use(--info-contrastText);

  font-size: 16px;
  font-family: "Gilroy";
  font-weight: 600;

  display: block;
  margin-left: auto;
  border: 2px solid rgba(255, 255, 255, 0.15);
  border-radius: 10px;
  padding: 0 10px;
  outline: none;
  transition: box-shadow 0.2s;

  &:focus,
  &:active {
    box-shadow: inset 0px 0px 5px 1px rgba(255, 255, 255, 0.15);
  }
}
