@import "sass/index";
@import "components/Input/_input.module.scss";

.input {
  text-overflow: ellipsis;
}

.inputRotateButton {
  margin-right: -10px;
}

.root {
  width: 100%;
  margin-top: spacing(3);

  &:focus-within,
  &:active,
  &:focus {
    .label {
      color: rgba(255, 255, 255, 0.5);
    }
  }
}
.label {
  font-size: 20px;
  font-family: Helvetica;
  color: rgba(255, 255, 255, 0.5);
}
.inputField {
  color: #ffffff;
  background-image: linear-gradient(
    180deg,
    rgba(255, 255, 255, 0.06) 1%,
    rgba(255, 255, 255, 0.08) 100%
  );
  border: 2px solid rgba(72, 53, 234, 0.57);
  box-shadow: 0 0 24px 0 rgba(72, 53, 234, 0.2);
  border-radius: spacing(1);
  border-color: rgba(72, 53, 234, 0.57);
  background-color: rgba(0, 0, 0, 0.09);

  &:focus-within,
  &:active,
  &:focus {
    border-color: rgba(72, 53, 234, 0.57);
    background-color: rgba(0, 0, 0, 0.09);
  }
}

.inputCopyButton {
  background: transparent !important;
  box-shadow: none;
  border: none;
  border-radius: 50%;
  width: 50px;
  height: 50px;
  padding: 5px;
}
