@import "sass/index";

.root {
  margin: 0 5px;
  width: 30px;
  height: 30px;
  border-radius: 8px;
  padding: 0 !important;

  background: linear-gradient(
    180deg,
    rgba(255, 255, 255, 0.06) 0%,
    rgba(255, 255, 255, 0.075) 100%
  );
}
.green {
  background: #44ffc3 !important;

  & path {
    color: #212438 !important;
    fill: #212438 !important;
  }
}
.label {
  width: 30px;
  height: 30px;
}
.icon {
  color: use(--template-modal-closeIcon-color);
  opacity: 0.7;
  width: 20px;
  height: 20px;

  & path {
    color: use(--template-modal-closeIcon-color);
    fill: use(--template-modal-closeIcon-color);
  }
}
