@import "sass/index";

.rainUser {
  margin: 0;
  white-space: nowrap;
  margin-right: 5px;
  line-height: 0;

  font-weight: bold;
}
.rainUserBlock {
  line-height: 100%;
  margin: 2px 0;
}
.rainCoin {
  position: relative;
  display: block;
  margin: 10px 0 12px;

  line-height: 100%;
  white-space: nowrap;

  & > img {
    position: static;
  }
}
.rainAmount {
  font-weight: bold;
}
.rainCoinImg {
  position: static;
  margin: 0 5px;
  margin-bottom: -2.5px;
}

.text {
  font-family: "HelveticaNeue", san-serif;
  line-height: 100%;

  font-size: 16px;
  color: inherit !important;
  letter-spacing: 0;
  overflow-wrap: anywhere;

  display: inline;

  // @media (min-width: 600px) {
  //   font-size: 16px;
  // }
}
.rainWrapper {
  display: flex;
  flex-direction: column;
  color: inherit;
}

.rainHeader {
  margin-bottom: 15px;
}
.rainBottom {
  margin-top: 15px;
}

.seeAllButton {
  margin-top: 10px;
}
