@import "sass/index";
@import "components/Input/_input.module.scss";

.root {
  width: 100%;
  margin-top: spacing(3);

  &:focus-within,
  &:active,
  &:focus {
    .label {
      color: #938ea0;
    }
  }
}
.label {
  color: #4b4857;
  font-size: 20px;
  font-family: Helvetica;
}
.inputField {
  color: #ffffff;
  background-image: linear-gradient(
    180deg,
    rgba(255, 255, 255, 0.06) 1%,
    rgba(255, 255, 255, 0.08) 100%
  );
  background-color: rgba(0, 0, 0, 0.13);
  border: 2px solid rgba(255, 255, 255, 0.1);
  box-shadow: 0 0 24px 0 rgba(72, 53, 234, 0.2);
  border-radius: spacing(1);

  &:focus-within,
  &:active,
  &:focus {
    border-color: rgba(72, 53, 234, 0.57);
    background-color: rgba(0, 0, 0, 0.09);
  }
}
