@import "sass/index";

.root {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 10px;
  grid-gap: 10px;

  & > * {
    margin: 0;
  }

  @media (max-width: $mobileScreen) {
    grid-template-columns: 1fr;
  }
}

.input {
  color: use(--template-statisticsBlock-tab-color);
  margin: 0;
  height: auto;
  border-radius: 8px;
  padding: 15px 5px 15px 5px;
}
.inputRoot {
  background: rgba(20, 25, 48, 0.3);

  border: 2px solid use(--secondary-dark);
  height: auto;
  border-radius: 10px;
}
.label {
  color: use(--template-statisticsBlock-tab-color, 0.5);
  font-size: 16px;
}

.coinImg {
  position: static !important;
}

.disable {
  opacity: 0.7;
  pointer-events: none;
}
