@import "sass/index";

@mixin icon {
  width: 24px;
  height: 24px;
  object-fit: contain;
}

.select {
  background-color: rgba(255, 255, 255, 0.6);
  height: 40px;
  &ExpandIcon {
    transform: scale(0.7);
    & polyline {
      stroke: use(--template-footer-select-color);
    }
  }
  &Item {
    padding: 10px;

    & > img {
      @include icon;
    }
  }
}
.selectIcon {
  @include icon;
}
.selectLabel {
  font-size: 18px;
  color: use(--template-footer-select-color);
}
.selectItem {
}
.dropDownIcon {
  & svg g {
    stroke: use(--template-footer-select-color);
  }
}
