@import "sass/index";

@keyframes openAnimation {
  0% {
    transform: scale(0.8);
    opacity: 0;
  }

  100% {
    transform: scale(1);
    opacity: 1;
  }
}

@keyframes closeAnimation {
  0% {
    transform: scale(1);
    opacity: 1;
  }

  100% {
    transform: scale(0.8);
    opacity: 0;
  }
}

.root {
  position: absolute;
  display: flex;
  justify-content: center;
  flex-direction: column;

  max-height: calc(100% - 64px);

  &__fullScreen {
    width: 100%;
    height: 100%;
    max-width: 100%;
    max-height: 100%;
    border-radius: 0;

    justify-content: flex-start;
    overflow: auto;
  }
}

.open {
  animation: openAnimation 0.15s ease-out forwards;
}

.close {
  animation: closeAnimation 0.15s ease-out forwards;
}

.closeIcon {
  position: absolute;
  top: 14px;
  right: 14px;

  & path {
    stroke: white;
  }
}
