@import "sass/index";

.wrapper {
  background: var(--template-page-background);
  background-color: Hsla(var(--template-page-backgroundColor));
  padding: 8px 0;

  position: relative;
  z-index: 6;
}
.root {
  height: 40px;
  width: 330px;

  transition: transform $defaultTransition;
  display: flex;
}

.hide {
  transition: transform 1s;
  transform: translateX(-15px);

  & svg {
    transform: rotate(180deg);
  }
}

.arrBlock {
  width: 12px;
  height: 40px;
  display: inline-block;
  background: use(--primary-dark);
  position: relative;
  cursor: pointer;
  border-radius: 6px 3px 3px 6px;
}

.arr {
  transition: transform 0.5s;
  font-size: 24px;
  margin: 50% -50%;
  fill: use(--template-chat-massage-author-color);

  & * {
    fill: use(--template-chat-massage-author-color);
  }
}

.buttonsWrapper {
  position: relative;
  width: calc(100% - (#{$arrBlockWidth} * 4));
  padding: 0 5px;
}

.selectInput {
  background: use(--template-footer-select-background);

  font-size: 13px;
  color: use(--template-footer-select-color);
}
