.root {
  overflow: auto;
}

.table {
  min-width: 470px;
  width: 100%;
  background: rgba(#ffffff, 0.1);
  border-radius: 10px;
  border-collapse: collapse;
  font-weight: 500;

  & th {
    text-align: start;
    width: 25%;
    color: white;
    padding: 10px;
  }
  & tr {
    color: rgba(#ffffff, 0.5);
  }
  & td {
    border-top: 1px solid rgba(#ffffff, 0.2);
    border-radius: 2px;
    padding: 10px;
  }
}

.coin {
  position: relative;
  left: 4px;
  top: 3px;
  user-select: none;
}
