@import "sass/index";
@import "components/Input/_input.module.scss";

.formInput {
  width: 100%;
}

.inputRoot {
  margin-top: spacing(0.5);
  font-size: 1.2rem;

  height: 50px;
}

.input {
  padding: 16px 12px;
  text-overflow: ellipsis;
}

.formInputLabel {
  font-size: 0.9rem;
  color: use(--template-input-label-color);
  &.Mui-focused {
    color: use(--template-input-label-color);
  }
}

.formInputAdornment {
  margin-left: -12px;
  position: static;
}

.inputCopyButton {
  margin-right: -10px;
}

.iconButton {
  display: block;
  cursor: pointer;
  background-color: transparent;
  border: none;
  border-radius: 50%;
  padding: 12px;
  transition: background-color 150ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;

  &:hover {
    background-color: rgba(gray, 0.1);
  }
  & svg {
    width: 24px;
    height: 24px;

    & > path {
      fill: gray;
    }
  }
}

.inputRotateButton {
  margin-right: -10px;
}

.textOverflow {
  text-overflow: ellipsis;
}
