@import "sass/index";

.leftColumn,
.rightColumn {
  display: flex;
  flex-direction: column;
}

.rightColumn {
  flex-grow: 0;
  max-width: 75%;
  flex-basis: 75%;
}

.flexRow {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.breadcrumbs {
  display: flex;
  align-items: center;

  & li a {
    text-decoration: none;
    color: use(--template-footer-colorTransparent);
  }

  & li:nth-child(2n) {
    margin: 0 spacing(2);
    color: use(--template-footer-colorTransparent);

    @media (max-width: $mobileScreen) {
      margin: 0 spacing(1);
    }
  }
}

.aboutBlock {
  padding-bottom: spacing(4);
  & a {
    text-decoration: none;
    color: use(--template-footer-colorSemiTransparent);
    font-weight: bold;
    font-family: "Gilroy", sans-serif;
    font-size: spacing(2);

    &:not(:first-child) {
      margin-left: spacing(4);
    }
  }
}

.termsBlock {
  display: flex;
  flex-wrap: wrap;
}

.termsElements {
  margin-top: 20px;
}

.description {
  display: flex;
  flex-direction: column;
  flex: 1;
}

.social {
  min-width: 116px;
  display: flex;
  align-items: center;
  margin-left: spacing(4.5);
  margin-right: spacing(7);
}

.socialIcon {
  padding: 0;
  height: spacing(4);
  width: spacing(4);
  background: transparent;
  // background: use(--template-footer-icons-twitter-background);
  border-radius: 50%;
  overflow: hidden;

  display: flex;
  align-items: center;
  justify-content: center;

  &:not(:first-child) {
    margin-left: 10px;
  }

  & img {
    width: 100%;
    height: 100%;

    // fill: use(--template-footer-icons-twitter-innerBackground);
  }
}

.select {
  width: 115px;
}

.link {
  text-decoration: none;
  color: use(--template-footer-colorTransparent);
  width: max-content;
}

.themeChange {
  margin-right: 10px;
}
