@import "sass/index";

.switch {
  display: flex;
  position: relative;
  width: 35px;
  height: 20px;
  justify-content: center;
}

.checkbox {
  position: absolute;
  width: 100%;
  height: 100%;
  margin: 0;
  opacity: 0;
  cursor: pointer;
}

.toggle {
  --switch-position: 60%;
  align-self: center;
  position: absolute;
  display: block;
  width: 20px;
  height: 20px;
  color: use(--template-gameCell-switch-activeColor);
  background-color: currentColor;
  border-radius: 50%;
  pointer-events: none;
  transition: transform 0.15s;
  transform: translateX(calc(-1 * var(--switch-position)));
  &On {
    transform: translateX(var(--switch-position));
  }
}

.track {
  position: absolute;
  width: 100%;
  height: 80%;
  background-color: use(--template-gameCell-switch-activeBackground);
  align-self: center;
  border-radius: 10px;
  pointer-events: none;
}
