@import "sass/index";

.swapBox {
  margin-right: 5px;
  transition: transform 0.4s;
  position: relative;
}
.img {
  height: 250px;
  width: 250px;
  object-fit: cover;
  border-radius: 10px;
  transition: all 0.4s !important;
}

.url {
  display: grid;
}

.playBox {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  z-index: 3;

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  border-radius: 10px;
  opacity: 0;
  background: rgba(0, 0, 0, 70%);
  transition: opacity 0.4s;

  &:hover {
    opacity: 1;

    & > svg {
      transform: scale(1);
    }
  }
}

.text {
  color: white;
  font-size: 0.75rem;
  letter-spacing: 1px;
  font-weight: bold;
  font-family: "Gilroy";
}

.icon {
  color: white;
  width: 60px;
  height: 60px;
  transition: transform 0.6s;
  transform: scale(2);
}

.etc {
  position: absolute;
  top: 0;
  right: 0;
  padding: 5px;
  background: #ffffff30;
  border-top-right-radius: 10px;
}
