.root {
  background: linear-gradient(
    180deg,
    rgba(255, 255, 255, 0.0620629) 0%,
    rgba(255, 255, 255, 0.0755026) 100%
  );
  box-shadow: 0px 0px 24px rgba(72, 53, 234, 0.196487) inset;
  border-radius: 8px;
  margin: 0 6px 6px 6px;
}

.tab {
  opacity: 1 !important;
  flex-basis: 50%;
  min-width: 50px !important;
  background-color: transparent !important;
}

.tabText {
  color: white;
  font-size: 1rem;
  line-height: 29px;
  letter-spacing: 1px;
  font-family: "Gilroy";
  font-weight: bold;
  text-transform: none;
}

div.wrapper {
  padding: 0 5px;
  box-sizing: border-box;
  height: 50px;
  width: 100%;
}

div.indicator {
  height: calc(100% - 8px);

  background: linear-gradient(
    180deg,
    rgba(255, 255, 255, 0.0620629) 0%,
    rgba(255, 255, 255, 0.0755026) 100%
  );
  box-shadow: 0px 0px 24px rgba(72, 53, 234, 0.196487) inset;
  border-radius: 8px;
}
