$arrowSize: 10px;
$disableBorder: $arrowSize solid transparent;

.root {
  width: 0;
  height: 0;

  position: absolute;
}

.bottom {
  border-left: $disableBorder;
  border-right: $disableBorder;

  border-bottom: $arrowSize solid;
  border-bottom-color: inherit;

  top: -$arrowSize;
  left: 50%;
  margin-left: -$arrowSize;
}

.top {
  border-left: $disableBorder;
  border-right: $disableBorder;

  border-top: $arrowSize solid;
  border-top-color: inherit;

  bottom: -$arrowSize + 1;
  left: 50%;
  margin-left: -$arrowSize;
}

.left {
  border-top: $disableBorder;
  border-bottom: $disableBorder;

  border-left: $arrowSize solid;
  border-left-color: inherit;

  bottom: 50%;
  right: -$arrowSize;
  margin-bottom: -$arrowSize;
}

.right {
  border-top: $disableBorder;
  border-bottom: $disableBorder;

  border-right: $arrowSize solid;
  border-right-color: inherit;

  bottom: 50%;
  left: -$arrowSize;
  margin-bottom: -$arrowSize;
}

:export {
  #{"arrowSize"}: $arrowSize;
}
