@import "sass/index";

$RectangleURL: "./Rectangle.svg";

.paper {
  display: flex;
  justify-content: center;
  background-color: #0a0d18;
  border-radius: 30px;
  background-image: url($RectangleURL);
  background-repeat: no-repeat;
  background-size: cover;
  transition: all 0.3s;
  overflow: hidden;

  @media (max-width: $mobileScreen) {
    height: 100% !important;
    max-height: 100% !important;
    width: 100% !important;
    max-width: 100% !important;

    border-radius: 0;
    background-image: linear-gradient(
      135deg,
      #3b1871 0%,
      rgba(11, 4, 21, 0.18) 48%,
      rgba(3, 1, 4, 0) 81%,
      #000000 100%
    );
  }
}
.closeIcon {
  position: absolute;
  top: 14px;
  right: 14px;

  & path {
    stroke: white;
  }
}
