@import "sass/index";

.wrapper {
  padding: 24px;
  border-radius: 10px 10px 0 0;

  display: flex;
  justify-content: flex-start;
  flex-wrap: wrap;
  background: use(--template-statisticsBlock-background);

  @media (max-width: $mobileScreen) {
    background: use(--template-statisticsBlock-mobile-background);
  }

  &_small {
    padding: 15px;
  }
}

.button {
  margin: 5px;
  font-family: "Gilroy", sans-serif;

  &_small {
    padding: 6px 10px;

    font-weight: 600;
    font-size: 11px;
    line-height: 13px;
    text-transform: uppercase;

    margin: 0 15px 15px 0;
  }
}
