@import "sass/index";

.body {
  display: table-row-group;
}

.tr {
  color: inherit;
  display: table-row;
  outline: 0;
  vertical-align: middle;
  color: use(--template-statisticsBlock-tab-tableBody);
}

.td {
  padding: 20px;

  display: table-cell;
  font-size: 0.875rem;
  text-align: center;
  font-family: "Gilroy";
  line-height: 15px;
  border-bottom: 1px solid use(--secondary-main, 0.2);
  letter-spacing: 0.01071em;
  vertical-align: inherit;
  font-weight: bold;

  @media (max-width: $mobileScreen) {
    padding: 10px 0;
    font-size: 0.75rem;
  }
}

.pointer {
  cursor: pointer;
}
